<template>
  <div class="Lwai">
    <el-form
      :model="ruleForm"
      ref="ruleForm"
      status-icon
      label-width="80px"
      class="demo-ruleForm Lcon"
    >
      <h3 class="use" style="text-align: center">用户登录</h3>
      <el-form-item
        label="用户名："
        
        prop="username"
        :rules="[
          { required: true, message: '用户名不能为空' },
          {
            min: 4,
            max: 12,
            message: '长度在 4 到 12 个字符',
            trigger: 'change',
          },
        ]"
      >
        <el-input
          placeholder="请输入用户名"
          v-model="ruleForm.username"
        ></el-input>
      </el-form-item>
      <el-form-item
        label="密码："
        prop="password"
        :rules="[
          { required: true, message: '密码不能为空' },
          { min: 6, max: 20, message: '密码要在 6 到 20 个字符之间' },
        ]"
      >
        <el-input
          type="password"
          placeholder="请输入密码"
          v-model="ruleForm.password"
          @keyup.enter.native="doLogin"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-row>
          <el-col :span="24">
            <div>
              <el-button type="primary" style="width: 100%" @click="doLogin"
                >登陆</el-button
              >
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <div>
              <el-link type="success" @click="toReg">用户注册</el-link>
            </div>
          </el-col>
          <el-col :span="12">
            <div>
              <el-link type="success">忘记密码</el-link>
            </div>
          </el-col>
        </el-row>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        username: "",
        password: "",
      },
    };
  },
  methods: {
    toReg() {
      this.$router.push({
        path: "/regist",
      });
    },
    doLogin() {
      // 登陆验证
      // let url = this.axios.urls.SYSTEM_USER_DOLOGIN;
      // console.log(url)
      // this.axios.get(url, {
      // 	params: this.ruleForm
      // }).then((response) => {
      // 	console.log(response);
      // 	if (response.data.code == 1) {
      // 		this.$message({
      // 			showClose:true,
      // 			message:response.data.msg,
      // 			type: 'success'
      // 		});
      // 	} else {
      // 		this.$message({
      // 			showClose:true,
      // 			message:response.data.msg,
      // 			type: 'error'
      // 		});
      // 	}

      // }).catch((response) => {
      // 	console.log(response);
      // });

      this.$refs.ruleForm.validate(async (valid) => {
        if (!valid) return this.$message.error("非法数据，请重新输入。");
        const { data: res } = await this.$http.post("login", this.ruleForm);
        if (res.status != 200) return this.$message.error(res.message);
        window.sessionStorage.setItem("token", res.token); // 成功则用window的全局变量sessionStorage(浏览器关闭即销毁)
        // this.$router.push("admin/index");
        this.$router.push("/index");
      });
    },
  },
};
</script>

<style>
.use {
  font: 1.5em bold;
  height: 35px;
}
.Lcon {
  border-radius: 10px;
  margin: 0px auto;
  width: 350px;
  padding: 30px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  text-align: left;
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.1);
}

.title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}

.Lwai {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding-top: 10%;
  background-image: url(data:image/svg+xml;base64,);
  /* background-color: #112346; */
  background-repeat: no-repeat;
  background-position: center right;
  background-size: 100%;
}
</style>
